<template>
  <div class="zd-common-component">
    <div class="zd-common-clearFix">
      <Button type="primary" class="zd-common-fl zd-common-pl11 zd-common-pr11 zd-common-mr10" custom-icon="zd-common-icon-add" @click="showAddModal">
        供应商档案
      </Button>
      <Button type="primary" class="zd-common-fl zd-common-pl11 zd-common-pr11 zd-common-mr10"
              custom-icon="zd-common-icon-import" @click="importHandle">
        导入
      </Button>
      <div class="zd-common-fr">
        <Select v-model="status" placeholder="状态" class="zd-common-input-h38 zd-common-w200 zd-common-fl zd-common-mr10"
                clearable>
          <Option value="0">启用</Option>
          <Option value="1">禁用</Option>
        </Select>
        <Input v-model="name" placeholder="供应商名称" class="zd-common-input-h38 zd-common-w200 zd-common-fl zd-common-mr10"/>
        <Button type="primary" class="zd-common-fl" @click="selectHandle" :loading="selectLoading">
          <span v-if="!selectLoading">查询</span>
          <span v-else>正在查询...</span>
        </Button>
      </div>
    </div>
    <div class="zd-common-mt16">
      <Table ref="currentRowTable" border :columns="columns" :data="data"></Table>
      <div class="zd-common-tc zd-common-mt20">
        <Page :total="total" :current="pageNum" :page-size="pageSize" @on-change="changePage" @on-page-size-change="changePageSize" show-sizer show-elevator show-total></Page>
      </div>
    </div>
    <!--添加供应商档案-->
    <Modal v-model="addModal" :mask-closable="false" @on-cancel="clearHandle" class="zd-cm-form">
      <h3 slot="header" class="zd-common-modal-header">
        <span>{{ modalTitle }}</span>
      </h3>
      <Form ref="addForm" :model="addForm" :rules="ruleValidate" :label-width="110">
        <FormItem label="供应商名称："  prop="name">
          <Input v-model.trim="addForm.name" placeholder="请输入供应商名称" :maxlength="50" class="zd-common-w300"
                 @on-blur="transCode"></Input>
        </FormItem>
        <FormItem label="供应商编码："  prop="code">
          <Input v-model.trim="addForm.code" placeholder="请输入供应商编码" :maxlength="50" class="zd-common-w300"></Input>
        </FormItem>
        <FormItem label="联系人：" >
          <Input v-model.trim="addForm.contacts" placeholder="请输入联系人" :maxlength="50" class="zd-common-w300"></Input>
        </FormItem>
        <FormItem label="联系人电话：" >
          <Input v-model.trim="addForm.phone" placeholder="请输入联系人电话" :maxlength="11" class="zd-common-w300"></Input>
        </FormItem>
        <FormItem label="地址："  prop="address">
          <Input v-model.trim="addForm.address" placeholder="请输入地址" :maxlength="50" class="zd-common-w300"></Input>
        </FormItem>
        <FormItem label="状态："  prop="status">
          <Select v-model="addForm.status" placeholder="请选择状态" class="zd-common-w300">
            <Option value="0">启用</Option>
            <Option value="1">禁用</Option>
          </Select>
        </FormItem>
        <FormItem label="资质文件：" >
          <div class="demo-upload-list" v-for="item in uploadScreenList">
            <template v-if="item.status === 'finished'">
              <img :src="item.url">
              <div class="demo-upload-list-cover">
                <Icon type="ios-eye-outline" @click.native="handleView(item.url)"></Icon>
                <Icon type="ios-trash-outline" @click.native="handleRemove(item.id)"></Icon>
              </div>
            </template>
            <template v-else>
              <Progress v-if="item.showProgress" :percent="item.percentage" hide-info></Progress>
            </template>
          </div>
          <Upload
            multiple
            name="filedata"
            ref="uploadScreen"
            :show-upload-list="false"
            :format="['jpg','jpeg','png']"
            type="drag"
            :action="serverPath+'/component/attachment!saveUploadFile2.action'"
            :max-size="2048"
            :on-success="handleScreenSuccess"
            :on-format-error="handleScreenFormatError"
            :on-exceeded-size="handleScreenMaxSize"
            :before-upload="handleBeforeUpload"
            class="zd-common-w72 zd-common-dib"
          >
            <div class="zd-common-w72 zd-common-h72 zd-common-lh72">
              <Icon type="ios-camera" size="20"></Icon>
            </div>
          </Upload>
          <Modal v-model="visible" :width="60">
            <h3 slot="header" class="zd-common-modal-header">
              <span>预览资质</span>
            </h3>
            <vue-scroll>
              <div class="zd-common-font-size-base zd-common-lh30 zd-common-tc zd-common-h500">
                <img :src="uploadPicUrl" v-if="visible" style="max-width: 100%">
              </div>
            </vue-scroll>

            <div slot="footer" class="zd-common-modal-footer zd-common-pt0"></div>
          </Modal>
        </FormItem>
      </Form>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt0">
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8"
                @click="handleSubmit('addForm')"
                :loading="saveLoading">
          <span v-if="!saveLoading">确定</span>
          <span v-else>正在保存...</span>
        </Button>
        <Button @click="clearHandle" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>
    <!--删除-->
    <Modal v-model="delModal" :mask-closable="false" :width="360">
      <h3 slot="header"></h3>
      <div class="zd-common-tc">
        <div class="zd-common-modal-waring zd-common-auto"></div>
        <p class="zd-common-primary-c zd-common-font-size-large zd-common-mt15">是否确认删除？</p>
      </div>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" class="zd-common-mr24 zd-common-w100 zd-common-h32 zd-common-lh8"
                :loading="saveLoading" @click="confirmDelHandle">
          <span v-if="!saveLoading">确定</span>
          <span v-else>正在删除...</span>
        </Button>
        <Button @click="delModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>
    <!--查看-->
    <Modal v-model="viewModal" :mask-closable="false">
      <h3 slot="header" class="zd-common-modal-header">
        <span>查看供应商档案</span>
      </h3>
      <div>
        <p class="zd-common-mb20"><span class="zd-common-dib zd-common-w100 zd-common-tr">供应商名称：</span>
          {{ allDetail.name }}
        </p>
        <p class="zd-common-mb20"><span class="zd-common-dib zd-common-w100 zd-common-tr">供应商编码：</span>
          {{ allDetail.code }}
        </p>
        <p class="zd-common-mb20"><span class="zd-common-dib zd-common-w100 zd-common-tr">联系人：</span>
          {{ allDetail.contacts }}
        </p>
        <p class="zd-common-mb20"><span class="zd-common-dib zd-common-w100 zd-common-tr">联系人电话：</span>
          {{ allDetail.phone }}
        </p>
        <p class="zd-common-mb20"><span class="zd-common-dib zd-common-w100 zd-common-tr">地址：</span>
          {{ allDetail.address }}
        </p>
        <p class="zd-common-mb20"><span class="zd-common-dib zd-common-w100 zd-common-tr">状态：</span>
          {{ allDetail.status==='0'?'启用':'禁用' }}
        </p>
        <p class="zd-common-mb20"><span class="zd-common-dib zd-common-w100 zd-common-tr">资质文件：</span>
          <span class="zd-common-primary-c" v-for="item in allDetail.picList" :key="item.id">{{ item.name }}</span>
        </p>
      </div>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" @click="viewModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">确定</Button>
        <Button @click="viewModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>
    <!--导入-->
    <Modal v-model="importModal" :mask-closable="false">
      <h3 slot="header" class="zd-common-modal-header">
        <span>导入供应商档案</span>
      </h3>
      <Form ref="importForm" :model="importForm" :rules="ruleValidate" :label-width="100" inline>
        <FormItem label="上传文件："  prop="fileName">
          <Upload
            ref="upload"
            action="/sr/storeroomData!doImportGoodsSupplier.action"
            accept="application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
            :before-upload="handleUpload"
          >
            <Button type="primary" icon="ios-cloud-upload-outline">选择文件</Button>
          </Upload>
          <div v-if="importForm.file !== null"> {{ importForm.file.name }} </div>
          <a :href="serverPath+'/sr/storeroomData!downloadGoodsSupplierTemplate.action'">下载模板</a>
          <input type="text" v-model="importForm.fileName" hidden/>
        </FormItem>
      </Form>
      <import-msg :importErrorMsg="importErrorMsg"></import-msg>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8"
                @click="importSubmit('importForm')" :loading="saveLoading">
          <span v-if="!saveLoading">确定</span>
          <span v-else>正在保存...</span>
        </Button>
        <Button @click="importModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import {utils} from "../../../comm/script/utils";
  import {transformPY} from "../../../comm/script/utils";
  import ImportMsg from '../../components/importMsg';
  export default {
    components:{
      ImportMsg
    },
    data(){
      const validateCode = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('不能为空'));
        } else {
          if(utils.regCode.test(value)){
            BaseAxios(
              {
                method: 'post',
                url: '/sr/storeroomData!validateGoodsSupplierCode.action',
                data: {
                  validateValue:value,
                  excludeValue:this.oldCode //修改时传，旧编码
                }
              })
              .then((response) => {
                if(!response.data){
                  callback(new Error('编码重复'));
                }
                callback();
              })
              .catch((error) => {
                callback(new Error('验证失败'));
                console.log(error);
              });
          }else{
            callback(new Error(utils.regCodeMsg));
          }
        }
      }
      return {
        //添加供应商档案
        addModal:false,
        oldCode:'',
        addForm:{
          name:'',
          code:'',
          contacts:'',
          phone:'',
          address:'',
          status:'0',
        },
        ruleValidate: {
          name: [
            { required: true, message:'不能为空', trigger: 'blur' }
          ],
          code: [
            { required: true, validator:validateCode, trigger: 'blur' }
          ],
          status: [
            { required: true, message:'不能为空', trigger: 'change' }
          ],
          fileName: [
            {required: true, message: '请选择文件', trigger: 'blur'}
          ],
        },
        modalTitle:'添加供应商档案',
        //上传资质
        visible: false,
        uploadPicUrl:'',
        uploadScreenList:[],
        uploadIds:[],
        //列表
        columns: [
          {
            type: 'index',
            align: 'center',
            title: '序号',
            width:65
          },
          {
            title: '供应商名称',
            align: 'center',
            key: 'name',
            render:(h,params)=>{
              return h('div', [
                h('span', {
                  class:'zd-common-primary-c zd-common-cp',
                  on: {
                    click: () => {
                      this.showViewModal(params.row.id);
                    }
                  }
                }, params.row.name),
              ])
            }
          },
          {
            title: '供应商编码',
            align: 'center',
            key: 'code'
          },
          {
            title: '联系人',
            align: 'center',
            key: 'contacts',
          },
          {
            title: '联系人电话',
            align: 'center',
            key: 'phone',
          },
          {
            title: '地址',
            align: 'center',
            key: 'address',
          },
          {
            title: '状态',
            align: 'center',
            key: 'status',
          },
          {
            title: '操作',
            align: 'center',
            key: 'action',
            width:160,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props:{
                    type:'primary',
                    ghost:true,
                  },
                  style: {
                    height: "28px",
                    padding:'0 15px 1px',
                    marginRight:'10px'
                  },
                  on: {
                    click: () => {
                      this.showEditModal(params.row.id);
                    }
                  }
                }, '编辑'),
                h('Button', {
                  props:{
                    type:'error',
                    ghost:true,
                  },
                  style: {
                    height: "28px",
                    padding:'0 15px 1px'
                  },
                  on: {
                    click: () => {
                      this.showDelModal(params.row.id);
                    }
                  }
                }, '删除')
              ]);
            }
          },
        ],
        data: [],
        pageSize:10,
        pageNum:1,
        total:0,
        selectLoading:false,
        //导入
        importModal:false,
        importForm:{
          file:null,
          fileName:''
        },
        importErrorMsg:[],
        //查询
        status:'0',
        name:'',
        saveLoading:false,
        //查看
        viewModal:false,
        allDetail:{},
        //编辑
        editId:'',
        //删除
        delModal:false,
      }
    },
    created(){
      this.getSupplierList(this.pageNum,this.pageSize,this.status,this.name);
    },
    methods:{
      //自动生成code
      transCode(){
        this.addForm.code = transformPY(this.addForm.name);
      },
      //添加供应商档案
      showAddModal(){
        this.modalTitle = "添加供应商档案";
        this.editId = '';
        this.addModal = true;
        this.uploadScreenList = [];
        this.uploadIds = [];
        this.$refs['addForm'].resetFields();
        this.addForm.contacts = "";
        this.addForm.phone = "";
        this.addForm.address = "";
      },
      saveOrUpdateGoodsSupplier(name,code,contacts,phone,address,status,attIds,editId) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!saveOrUpdateGoodsSupplier.action',
          data:{
            "goodsSupplier.name":name,
            "goodsSupplier.code":code,
            "goodsSupplier.contacts":contacts,
            "goodsSupplier.phone":phone,
            "goodsSupplier.address":address,
            "goodsSupplier.status":status,
            "goodsSupplier.id":editId,
            "attIds":attIds
          },
        })
          .then((response) => {
            this.saveLoading=false;
            this.addModal=false;
            this.getSupplierList(this.pageNum,this.pageSize,this.status,this.name);
            this.postSuccessTip('保存成功');
          })
          .catch((error) => {
            this.saveLoading=false;
            this.addModal=false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //保存验证
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.saveLoading=true;
            this.saveOrUpdateGoodsSupplier(this.addForm.name,this.addForm.code,this.addForm.contacts,this.addForm.phone,this.addForm.address,this.addForm.status,this.uploadIds.join(','),this.editId);
          }
        })
      },
      //上传资质
      handleView(pic){
        this.uploadPicUrl = pic;
        this.visible = true;
      },
      handleRemove(id){
        this.uploadScreenList.splice(this.uploadIds.indexOf(id), 1);
        this.uploadIds.splice(this.uploadIds.indexOf(id), 1);
      },
      handleScreenSuccess (res, file) {
        this.uploadIds.push(res.id);
        file.url = this.serverPath+"/sr/storeroomData!showTempPic.action?attachmentId="+res.id;
        file.id = res.id;
        this.uploadScreenList = this.$refs.uploadScreen.fileList;
      },
      handleScreenFormatError(file){
        this.postWarningTip(file.name+"图片格式错误");
      },
      handleScreenMaxSize(file){
        this.postWarningTip(file.name+"图片太大",'最多上传2M大小的图片');
      },
      handleBeforeUpload(file){
        const check = this.$refs.uploadScreen.fileList.length < 5;
        if (!check) {
          this.postWarningTip('最多上传5张');
        }else{
          for(let item of this.$refs.uploadScreen.fileList){
            if(file.name === item.name){
              this.postWarningTip('图片已存在');
              return false;
            }
          }
        }
        return check;
      },
      //导入
      importHandle(){
        this.importErrorMsg = [];
        this.importModal = true;
        this.$refs['importForm'].resetFields();
        this.importForm.file = null;
      },
      handleUpload(file) {
        if(!utils.regImportType.test(file.name)){
          this.postWarningTip(utils.regImportTypeMsg);
        }else{
          this.importForm.file = file;
          this.importForm.fileName = file.name;
          this.$refs['importForm'].validateField('fileName');
        }
        return false;
      },
      importSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.saveLoading=true;
            this.validateImportGoodsSupplier();
          }
        })
      },
      saveImport(){
        let formData = new FormData();
        formData.append('upload',this.importForm.file);
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!doImportGoodsSupplier.action',
          data:formData,
          processData: false,
          contentType: false,
          transformRequest: [function (data) {
            return data;
          }],
        })
          .then((response) => {
            this.saveLoading=false;
            this.importModal=false;
            this.importForm.file = null;
            this.getSupplierList(this.pageNum,this.pageSize,this.status,this.name);
          })
          .catch((error) => {
            this.saveLoading=false;
            this.importModal=false;
            this.postErrorTip('请求失败');
            this.$refs.upload.value = '';
            console.log(error);
          });
      },
      //导入前验证
      validateImportGoodsSupplier() {
        let formData = new FormData();
        formData.append('upload',this.importForm.file);
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!validateImportGoodsSupplier.action',
          data:formData,
          processData: false,
          contentType: false,
          transformRequest: [function (data) {
            return data;
          }],
        })
          .then((response) => {
            const aData = response.data;
            this.importErrorMsg = aData;
            if(aData.length<=0){
              this.saveImport();
            }else{
              this.saveLoading=false;
            }
          })
          .catch((error) => {
            this.saveLoading=false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //列表
      getSupplierList(pageNum,pageSize,status,name) {
        this.data = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getGoodsSupplierPage.action',
          data:{
            pageNum:pageNum,
            pageSize:pageSize,
            status:status,
            name:name
          },
        })
          .then((response) => {
            let aData = response.data;
            if(aData && aData.list.length>0){
              aData.list.map((item)=>{
                this.data.push({
                  id:item.id,
                  name:item.name,
                  code:item.code,
                  contacts:item.contacts,
                  phone:item.phone,
                  address:item.address,
                  status:item.statusName
                })
              })
            }
            this.total = aData.totalCount;
            this.selectLoading = false;
          })
          .catch((error) => {
            this.selectLoading = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //分页
      changePage(num) {
        this.pageNum = num;
        this.getSupplierList(this.pageNum,this.pageSize,this.status,this.name);
      },
      changePageSize(num) {
        this.pageSize = num;
        this.getSupplierList(this.pageNum,this.pageSize,this.status,this.name);
      },
      //查询
      selectHandle(){
        this.selectLoading = true;
        this.getSupplierList(this.pageNum,this.pageSize,this.status,this.name);
      },
      //查看
      showViewModal(id){
        this.viewModal = true;
        this.getSupplierInfo(id);
      },
      getSupplierInfo(id) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getGoodsSupplierInfo.action',
          data:{
            id:id
          },
        })
          .then((response) => {
            let aData = response.data;
            this.allDetail = aData;
            this.addForm.name = aData.name;
            this.addForm.code = aData.code;
            this.addForm.contacts = aData.contacts;
            this.addForm.phone = aData.phone;
            this.addForm.address = aData.address;
            this.addForm.status = aData.status;
            aData.picList.map((item)=>{
              this.uploadIds.push(item.id);
              this.uploadScreenList.push({
                id:item.id,
                url:this.serverPath+item.url,
                status:'finished'
              })
            })
          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //编辑
      showEditModal(id){
        this.modalTitle = "修改供应商档案";
        this.editId = id;
        this.addModal = true;
        this.uploadScreenList = [];
        this.uploadIds = [];
        this.getSupplierInfo(id);
      },
      clearHandle(){
        this.$refs['addForm'].resetFields();
        this.addModal=false;
      },
      //删除
      showDelModal(id){
        this.editId = id;
        this.delModal = true;
      },
      deleteGoodsSupplier(id) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!deleteGoodsSupplier.action',
          data:{
            id:id
          },
        })
          .then((response) => {
            let aData = response.data;
            this.getSupplierList(this.pageNum,this.pageSize,this.status,this.name);
          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      confirmDelHandle(){
          this.deleteGoodsSupplier(this.editId);
      }
    },
    mounted(){
      this.uploadScreenList = this.$refs.uploadScreen.fileList;
    }
  }
</script>
<style lang="less" scoped>
  .ivu-table .demo-table-info-row td{
    background-color: rgba(28, 144, 243, 0.1);
  }
</style>



